html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Overpass", sans-serif;
  background: url(\build\img\backgroundB.webp) repeat fixed;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%; }

.container {
  width: 90%;
  margin: 0 auto;
  max-width: 120rem; }

.text-center {
  text-align: center; }

h1 {
  font-size: 3.8rem; }

h2 {
  font-size: 3.2rem;
  text-transform: uppercase;
  margin: 0;
  padding: 2rem 0; }

h3 {
  font-size: 2.8rem; }

p {
  font-size: 2.2rem; }

a {
  text-decoration: none;
  color: #000000; }

.button {
  font-size: 2rem;
  padding: 0.5rem 2rem;
  border-radius: .5rem;
  background-color: #f9a312;
  border: none; }

.header {
  background-image: url(\build\img\backgroundC.webp);
  background-position: center;
  background-size: cover;
  height: 100vh;
  width: 100vw;
  text-align: center;
  position: relative; }
  @media (min-width: 768px) {
    .header {
      max-width: 100%; } }

.title {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.4);
  width: 80%;
  height: 20%;
  position: absolute;
  border-radius: 2rem;
  top: 50%;
  left: 10%;
  display: flex;
  align-items: center;
  backdrop-filter: blur(5px); }
  @media (min-width: 768px) {
    .title {
      width: 30%;
      left: 35%; } }
  .title h1 {
    margin: 0 auto;
    text-transform: uppercase; }
  .title .front-end {
    font-size: 2rem;
    font-weight: 400;
    text-transform: none; }

.nav {
  position: fixed;
  width: 100%;
  background-color: #000000;
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 999; }
  .nav .nav-content {
    visibility: hidden;
    opacity: 0;
    transition: opacity 1s linear, height linear;
    align-items: flex-start;
    height: 0; }
    @media (min-width: 768px) {
      .nav .nav-content {
        visibility: visible;
        opacity: 1;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        height: 100%; }
        .nav .nav-content a {
          margin-right: 1.5rem; } }
  .nav .active-mobile {
    visibility: visible;
    opacity: 1;
    display: flex;
    flex-direction: column;
    height: 100%; }
    @media (min-width: 768px) {
      .nav .active-mobile {
        flex-direction: row; } }
  .nav a {
    color: #ffffff;
    text-decoration: none;
    font-size: 2.8rem; }
    .nav a:hover, .nav a .active {
      font-weight: 700; }

.mobile-menu {
  width: 3rem;
  width: 90%;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .mobile-menu {
      display: none; } }
  .mobile-menu img {
    margin: 0 100% 0 0; }

.about-me {
  background-color: #333333;
  padding-bottom: 4rem;
  color: #ffffff; }
  @media (min-width: 768px) {
    .about-me .about-me-content {
      display: grid;
      grid-template-columns: 1fr 2fr;
      column-gap: 2rem;
      align-items: center; } }
  .about-me .about-me-content img {
    width: 90%;
    border-radius: 50%;
    margin-left: 1rem; }
  .about-me .about-me-content p:first-of-type {
    margin-top: 0; }

.skills h2 {
  text-transform: uppercase;
  margin: 0;
  padding: 2rem;
  color: #ffffff; }

.skills-logos {
  padding-bottom: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 2rem; }
  @media (min-width: 768px) {
    .skills-logos {
      grid-template-columns: repeat(3, 1fr); } }
  .skills-logos img {
    width: 100%;
    height: 20rem;
    transition: transform 3s cubic-bezier(0.18, 0.89, 0.32, 1.28); }
    .skills-logos img:hover {
      transform: rotate(360deg); }
  .skills-logos p {
    color: #ffffff; }

#contact {
  padding-bottom: 3rem; }
  #contact h3, #contact h2, #contact p {
    color: #ffffff; }

.form {
  background-color: #000000;
  border-radius: 1rem;
  padding: 2rem 0; }
  .form .fields {
    display: flex;
    flex-direction: column;
    row-gap: 3rem; }
    @media (min-width: 768px) {
      .form .fields {
        display: grid;
        grid-template-columns: repeat(2, 1fr); } }

.field {
  margin: 0 2rem 1rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #f9a312; }
  .field label {
    font-size: 2rem;
    margin-bottom: .5rem; }
  .field input, .field textarea {
    width: 100%;
    height: 5rem;
    border: none;
    font-size: 1.5rem;
    background-color: #333333;
    margin: 0 auto;
    border-radius: .5rem;
    color: #ffffff; }
  @media (min-width: 768px) {
    .field {
      flex-direction: row; }
      .field label {
        flex: 0 0 10rem; }
      .field input, .field textarea {
        flex: 1; } }
  .field:last-of-type {
    grid-column: 1 / 3; }
    .field:last-of-type textarea {
      height: 5rem;
      width: 100%; }

.submiting {
  display: flex;
  justify-content: flex-end; }
  .submiting .button {
    margin: 2rem 2rem 0 0; }

.button {
  font-size: 2rem;
  padding: 0.5rem 2rem;
  margin: 2rem 2rem 0 0;
  border-radius: .5rem;
  background-color: #f9a312;
  border: none; }

.grid-media {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  .grid-media .media {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .grid-media .media img {
      height: 7rem;
      margin: 1rem auto; }
      .grid-media .media img:hover {
        height: 8rem;
        margin: 0 auto; }
      @media (min-width: 768px) {
        .grid-media .media img {
          height: 10rem; }
          .grid-media .media img:hover {
            height: 11rem;
            margin: 0 auto; } }

.footer {
  background-color: #333333;
  color: #ffffff;
  padding: 2rem; }
  .footer p {
    margin: 1rem; }

#projects {
  background-color: #333333;
  color: #ffffff; }

.projects {
  display: flex;
  gap: 2rem;
  padding-bottom: 2rem;
  flex-direction: column; }
  @media (min-width: 768px) {
    .projects {
      flex-direction: row; } }

.project-tenis {
  position: relative; }
  .project-tenis img {
    height: 100%;
    width: 100%;
    z-index: 1;
    opacity: 0.5; }

.project-coffe {
  position: relative; }
  .project-coffe img {
    height: 100%;
    width: 100%;
    z-index: 1;
    opacity: 0.5; }

.project-tenis, .project-coffe {
  width: 100%;
  max-height: 50rem;
  border: 5px solid #ffffff; }
  .project-tenis:hover .project-content, .project-coffe:hover .project-content {
    height: 100%;
    z-index: 3; }
    .project-tenis:hover .project-content h3, .project-tenis:hover .project-content .btns, .project-tenis:hover .project-content p, .project-coffe:hover .project-content h3, .project-coffe:hover .project-content .btns, .project-coffe:hover .project-content p {
      opacity: 1; }

.project-content {
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
  top: 0;
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 0%;
  transition: height 1s ease-out, z-index 0s linear; }
  .project-content h3, .project-content .btns, .project-content p {
    opacity: 0;
    transition: opacity 0.5s linear 0.8s; }
  .project-content h3 {
    margin: 10% auto 0 auto; }
  .project-content .btns {
    margin: 10% 0 0 0; }
    @media (min-width: 768px) {
      .project-content .btns {
        margin: 20% 0 0 0; } }

.btns {
  display: flex;
  justify-content: space-evenly; }
  .btns .button {
    margin: 0; }
    @media (min-width: 1024px) {
      .btns .button {
        padding: 2rem 4rem; } }

.tap-project {
  display: block; }
  @media (min-width: 768px) {
    .tap-project {
      display: none; } }

.disabled {
  pointer-events: none; }
